import Navbar from "../../components/navbar/Navbar";
import Featured from "../../components/featured/Featured";
import "./home.scss";
import { Tabs, Tab } from "@material-ui/core"
import ViewGeneral from "../../components/viewGeneral/ViewGeneral";
import { useState } from "react";

const styleTabs = { color: '#fff' }
const styleTemp = {
  color: '#fff',
  minHeight: '300px',
  textAlign: 'center'
}

export default function Home({ type }) {
  const [valueTab, setValueTab] = useState(0)
  const handleChangeTabs = (event, newValue) => {
    setValueTab(newValue);
  };
  return (
    <div className="home">
      <Navbar isHome />
      <Featured type={type} />
      <Tabs value={valueTab}
        onChange={handleChangeTabs}
        aria-label="basic tabs example"
        // indicatorColor="secondary"
        centered
      >
        <Tab label="General" id={"simple-tab-0"} style={styleTabs} />
        <Tab label="Follows" id={"simple-tab-1"} style={styleTabs} />
        <Tab label="Premiun" id={"simple-tab-2"} style={styleTabs} />
      </Tabs>
      {valueTab === 0 && <ViewGeneral />}
      {valueTab === 1 && <h1 style={styleTemp}>Here will load only follows</h1>}
      {valueTab === 2 && <h1 style={styleTemp}>Here will load only Premium</h1>}
    </div>
  );
};

